<template>
  <div class="index">
    <bread-nav :nav="nav"></bread-nav>
    <main-part v-loading="newLoading">
      <div class="content">
        <el-form label-width="120px" label-position="left"
                 :model="form" ref="form" :rules="rules">
          <el-form-item label="年级: " prop="gradeId">
            <el-select class="input" v-model="form.gradeId" @change="onSwitchGradeChange">
              <el-option
                      v-for="item in gradeList"
                      :label="item.name" :value="item.id" :key="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="年级组长缺席: ">
            <el-select class="input" v-model="form.gradeLeaderAbsentId" multiple>
              <el-option
                      v-for="(gradeLeaderItem,gradeLeaderIdx) in gradeLeaderList"
                      :label="gradeLeaderItem.gradeLeaderName"
                      :value="gradeLeaderItem.gradeLeaderId"
                      :key="gradeLeaderIdx"/>
            </el-select>
          </el-form-item>
          <el-form-item label="年级组长迟到: ">
            <el-select class="input" v-model="form.gradeLeaderLateId" multiple>
              <el-option
                      v-for="(gradeLeadersItem,gradeLeadersIdx) in gradeLeaderList"
                      :label="gradeLeadersItem.gradeLeaderName"
                      :value="gradeLeadersItem.gradeLeaderId"
                      :key="gradeLeadersIdx"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="班主任缺席: ">
            <el-select class="input" v-model="form.headmasterAbsentId" multiple>
              <el-option
                      v-for="(headmasterItem,headmasterIdx) in headmasterList"
                      :label="headmasterItem.headmasterName"
                      :value="headmasterItem.headmasterId"
                      :key="headmasterIdx"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="班主任迟到: ">
            <el-select class="input" v-model="form.headmasterLateId" multiple>
              <el-option
                      v-for="(headmastersItem,headmastersIdx) in headmasterList"
                      :label="headmastersItem.headmasterName"
                      :value="headmastersItem.headmasterId"
                      :key="headmastersIdx"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="班主任助理就位: ">
            <el-select class="input" v-model="form.headmasterAssistantInPlaceId" multiple>
              <el-option
                      v-for="(item,AssistantIdx) in headmasterAssistantList"
                      :label="item.headmasterAssistantInPlaceName"
                      :value="item.headmasterAssistantInPlaceId"
                      :key="AssistantIdx"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="检查日期: " prop="inspectDate">
            <el-date-picker
                    type="date"
                    v-model="form.inspectDate"
                    value-format="yyyy-MM-dd HH:mm:ss"
                    placeholder="选择日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="检查人：" prop="inspectUser">
            <el-input placeholder="请输入"
                      v-model="form.inspectUser"
                      clearable></el-input>
          </el-form-item>
        </el-form>
        <div class="btns">
          <div class="btn-submit mr15" @click="submitEntry">保存</div>
        </div>
      </div>
    </main-part>
  </div>
</template>
<script>
  export default {
    data () {
      let vm = this
      return {
        tabPick: vm.$route.query.tabPick,
        gradeId: vm.$route.query.gradeId,
        inspectDate: vm.$route.query.inspectDate,
        nav: [{name: null, path: 'go(-1)'}, {name: null, path: ''}],
        gradeList: [],
        form: {
          gradeId: 1,
          gradeLeaderAbsentId: [],
          gradeLeaderLateId: [],
          headmasterAbsentId: [],
          headmasterLateId: [],
          headmasterAssistantInPlaceId: [],
          inspectDate: null,
          inspectUser: null
        },
        rules: {
          gradeId: {required: true, message: '请选择年级', trigger: 'change'},
          inspectDate: {required: true, message: '请选择检查日期', trigger: 'change'},
          inspectUser: {required: true, message: '请输入检查人', trigger: 'blur'}
        },
        // 年级组长
        gradeLeaderList: [],
        // 班主任
        headmasterList: [],
        // 班主任助理
        headmasterAssistantList: [],

        // 详情
        Details: [],

        text: '保存成功',
        newLoading: false,
      }
    },
    methods: {
      // 获取年级列表
      async getGradeList () {
        this.gradeList = await this.$ajax({
          url: '/grade/queryGradeList.do',
        })
      },
      // 切换年级
      async onSwitchGradeChange (val) {
        this.form.gradeLeaderAbsentId = []
        this.form.gradeLeaderLateId = []
        this.form.headmasterAbsentId = []
        this.form.headmasterLateId = []
        this.form.headmasterAssistantInPlaceId = []
        let PersonnelList = await this.$ajax({
          url: '/assess/queryPersonnelList.do',
          query: {
            id: val
          }
        })
        // console.log(PersonnelList)
        this.gradeLeaderList = PersonnelList.gradeLeaderItemList
        this.headmasterList = PersonnelList.headmasterItemList
        this.headmasterAssistantList = PersonnelList.headmasterAssistantItemList
      },
      // 处理面包屑
      getName: function (val) {
        switch (val) {
          case 'MORNING_LESSON':
            return '录入早自习到岗检查记录'
          case 'BREAK_EXERCISE':
            return '录入课间操跟跑检查记录'
          case 'EVENING_LESSON':
            return '录入晚自习到岗检查记录'
          case 'REGULAR_MEETING':
            return '录入例会到岗检查记录'
          default:
            return val
        }
      },
      /**
       * @return {boolean}
       */
      // 这个方法判断一个对象里面的数组是否为空
      objHasFieldIsEmpty (arr) {
        let hasEmpty = false
        for (let j in arr) {
          if(arr[j].length){
            hasEmpty = true
            break
          }
        }
        return hasEmpty
      },
      // 提交
      async submitEntry () {
        await this.$refs.form.validate()
        let form = this.$cloneObj(this.form)
        delete form.gradeId
        delete form.inspectType
        delete form.inspectDate
        delete form.inspectUser
        if (!this.objHasFieldIsEmpty(form)){
          this.$message.warning('请选择缺席或迟到人员')
          return
        }
        let url = '/assess/addAssessInspectRecord.do'
        if (this.gradeId) {
          url = '/assess/changeAssessInspectRecord.do'
          this.text = '编辑成功'
        }
        await this.$ajax({
          url,
          query: this.form
        })
        this.$remind(this.text)
        this.$router.push(`/student-work/appraisal/early-selfStudy/recording?tabPick=` + this.tabPick + ``)
      },
      //获取班主任班助年级组长
      async personnelList () {
        let PersonnelList = await this.$ajax({
          url: '/assess/queryPersonnelList.do',
          query: {
            id: this.form.gradeId
          }
        })
        this.gradeLeaderList = PersonnelList.gradeLeaderItemList
        this.headmasterList = PersonnelList.headmasterItemList
        this.headmasterAssistantList = PersonnelList.headmasterAssistantItemList
      },
      // 获取详情
      async getDetails () {
        this.Details = await this.$ajax({
          url: '/assess/queryAssessInspectRecord.do',
          query: {
            gradeId: this.gradeId,
            inspectDate: this.inspectDate,
            inspectType: this.tabPick,
          }
        })
        let PersonnelList = await this.$ajax({
          url: '/assess/queryPersonnelList.do',
          query: {
            id: this.Details.gradeId
          }
        })
        this.gradeLeaderList = PersonnelList.gradeLeaderItemList
        this.headmasterList = PersonnelList.headmasterItemList
        this.headmasterAssistantList = PersonnelList.headmasterAssistantItemList

        this.form.inspectUser = this.Details.inspectUser
        this.form.inspectDate = this.Details.inspectDate
        this.form.gradeId = this.Details.gradeId
        this.Details.headmasterLate.forEach(e => {
          this.form.headmasterLateId.push(e.userId)
        })
        this.Details.headmasterAssistantInPlace.forEach(e => {
          this.form.headmasterAssistantInPlaceId.push(e.userId)
        })
        this.Details.headmasterAbsent.forEach(e => {
          this.form.headmasterAbsentId.push(e.userId)
        })
        this.Details.gradeLeaderLate.forEach(e => {
          this.form.gradeLeaderLateId.push(e.userId)
        })
        this.Details.gradeLeaderAbsent.forEach(e => {
          this.form.gradeLeaderAbsentId.push(e.userId)
        })
      }
    },
    async mounted () {
      this.newLoading = true
      await this.getGradeList()
      this.nav[0].name = this.$enums.InspectType.getName(this.tabPick)
      this.nav[1].name = this.getName(this.tabPick)
      this.form.inspectType = this.tabPick
      await this.personnelList()

      if (this.gradeId) {
        await this.getDetails()
      }

      this.newLoading = false
    }
  }
</script>
<style lang="sass" scoped>
  .content
    width: 100%
    margin: 0 auto
    padding: 20px 0 0 30px
    border-radius: 10px
    /deep/ .el-input__inner
      width: 300px
    .btns
      margin-left: 120px
</style>
